import { Component, OnInit } from "@angular/core";
import { CompanyService } from "../company.service";
import { Job } from "../Job";
import { ToastrService } from "ngx-toastr";

import { Location } from "@angular/common";

@Component({
  selector: "app-edit-job",
  templateUrl: "./edit-job.component.html",
  styleUrls: ["./edit-job.component.css"]
})
export class EditJobComponent implements OnInit {
  job: Job = new Job();
  allJobs = new Array<Job>();

  id: number;

  constructor(
    private service: CompanyService,
    private location: Location,
    private toastr: ToastrService
  ) {}

  ngOnInit() {
    this.getJobs();
  }

  getJobs() {
    this.service.getAllJobs().subscribe(
      data => (this.allJobs = data),
      error => console.log(error)
    );
  }

  addJob(job: any) {
    this.service.addJob(this.job).subscribe(
      response => console.log(response),
      error => console.log(error)
    );
  }

  back() {
    this.location.back();
  }

  getId(id: number) {
    this.id = id;
    this.delete(id);
  }

  delete(id: number) {
    this.service.deleteJob(id).subscribe(
      data => {console.log(data); this.getJobs(); this.showToasterSuccees()},

      error => {
        this.showToasterError(), console.log(error);
      }
    );
  }

  showToasterError() {
    this.toastr.error("Cant delete Job is assigned to employee");
  }
  showToasterSuccees() {
    this.toastr.success("Job was deleted");
  }
}
