import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Employee } from "./employee";
import { Observable } from "rxjs";
import { Job } from "./Job";

@Injectable({
  providedIn: "root"
})
export class CompanyService {
  constructor(private http: HttpClient) {}

  getAllEmployees(): Observable<Employee[]> {
    return this.http.get<Employee[]>(`http://localhost:8090/employees`);
  }

  getAllJobs(): Observable<Job[]> {
    return this.http.get<Job[]>(`http://localhost:8090/employees/job`);
  }

  getEmployeeById(id: any): Observable<Employee> {
    return this.http.get<Employee>(`http://localhost:8090/employees/${id}`);
  }

  delete(id: number): Observable<Employee> {
    return this.http.delete<Employee>(`http://localhost:8090/employees/${id}`);
  }

  add(employee: Employee): Observable<Employee> {
    return this.http.post<Employee>(
      `http://localhost:8090/employees`,
      employee
    );
  }

  edit(id: any, value: any): Observable<Employee> {
    return this.http.put<Employee>(
      `http://localhost:8090/employees/${id}`,
      value
    );
  }

  addJob(job: Job): Observable<Job> {
    return this.http.post<Job>(`http://localhost:8090/employees/job`, job);
  }

  deleteJob(id: number): Observable<Job> {
    return this.http.delete<Job>(`http://localhost:8090/employees/job/${id}`);
  }
}
