import { Component, OnInit } from "@angular/core";
import { Employee } from "../employee";
import { Job } from "../Job";
import { ActivatedRoute } from '@angular/router';

import { Location } from '@angular/common';
import { CompanyService } from "../company.service";
import { NgForm } from '@angular/forms';

@Component({
  selector: "app-edit",
  templateUrl: "./edit.component.html",
  styleUrls: ["./edit.component.css"]
})
export class EditComponent implements OnInit {

  
  id: number;
  employee: Employee = new Employee();
  jobs: Array<Job> = [];

  constructor(
    private service: CompanyService,
    private route: ActivatedRoute,
    private location: Location
  ) {}

  ngOnInit() {
    this.getEmployeeForEdit();
    this.getAllRoles();
  }

  getAllRoles(): any {
    this.service.getAllJobs().subscribe(
      response => {this.jobs = response;},
      error => console.log(error)
    );
  }

  getEmployeeForEdit() {
    this.id = this.route.snapshot.params.id;
    if (this.id) {
      this.service.getEmployeeById(this.id).subscribe(
        response => {this.employee = response;},
        error => console.log(error)
      );
    }
  }

  edit(id: number, form: NgForm) {this.service.edit(this.employee, id).subscribe(
    successResponse => {this.location.back()},
        error=>console.log(error)
      );
  }

  
}
